.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Media Queries */
@media (max-width: 952px){
    
  nav ul li a{
    font-size: 22px;
    
  }
  .header-content p 
  {
      font-size: 22px;
  }
  .icon 
  {
      width: 100px;
      
  }
  .icon p 
  {
      font-size: 12px;
  }
  .race h1
  {
    font-size: 72px;
  }
  .race 
  {
    padding-top: 100px;
  }
  #race-action
  {
    font-size: 72px;
  }
  
}
@media (max-width: 858px){
  .checkbtn{
    display: block;
  }
  ul{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 80px;
    left: -100%;
    text-align: center;
    transition: all .5s;
    z-index: 1;
    background-image: linear-gradient(to right, #ce9690 , #3d86ab);
  }
  nav ul li{
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }
  nav ul li a{
    font-size: 20px;
  }
  #check:checked ~ ul{
    left: 0;
  }
  .header-content img 
  {
    width: 60%;
  }
  .race h1
  {
    font-size: 52px;
  }
  #race-action
  {
    font-size: 52px;
  }
  .header-content p 
  {
      font-size: 18px;
  }
  .icon 
  {
      width: 100px;
  }
  .form
  {
    width: 300px;
  }
}
@media (max-width: 650px){
  

  .header-content p 
  {
      font-size: 12px;
  }
  
}
