body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.story-container {
  position: relative;
  width: 100%;
  height: 2700vh;
}
  .story-image-container {
    width: 100%;
    min-height: 100vh;
    position: sticky;
    top: 0;
    bottom: 0;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url('https://raw.githubusercontent.com/moinhaider520/test-7/main/public/assets/story/story%20001.jpg');
  }

  /* Media Queries */
@media (max-width: 952px){
    
  nav ul li a{
    font-size: 22px;
    
  }
  .header-content p 
  {
      font-size: 22px;
  }
  .icon 
  {
      width: 100px;
      
  }
  .icon p 
  {
      font-size: 12px;
  }
  .race h1
  {
    font-size: 72px;
  }
  .race 
  {
    padding-top: 100px;
  }
  #race-action
  {
    font-size: 72px;
  }
  
}
@media (max-width: 858px){
  .checkbtn{
    display: block;
  }
  ul{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 80px;
    left: -100%;
    text-align: center;
    transition: all .5s;
    z-index: 1;
    background-image: linear-gradient(to right, #ce9690 , #3d86ab);
  }
  nav ul li{
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }
  nav ul li a{
    font-size: 20px;
  }
  #check:checked ~ ul{
    left: 0;
  }
  .header-content img 
  {
    width: 60%;
  }
  .race h1
  {
    font-size: 52px;
  }
  #race-action
  {
    font-size: 52px;
  }
  .header-content p 
  {
      font-size: 18px;
  }
  .icon 
  {
      width: 100px;
  }
  #form
  {
    width: 300px;
  }
}
@media (max-width: 650px){
  

  .header-content p 
  {
      font-size: 12px;
  }
  
}